
.header {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 60px;
  color: #fff;
  padding: 0 25px;
  width: 100%;
  z-index: 2;
}
.header .header-logo {
    font-size: 30px;
    font-weight: bold;
    float: left;
}
.header .header-nav {
    float: right;
}
.header .header-nav a {
      color: #fff;
      margin-left: 20px;
}
.wave {
  width: 100%;
  height: 300px;
}
#pop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.banner {
  width: 100%;
  height: 500px;
  background: #00afe9;
  color: #fff;
}
.banner .banner-img {
    width: 300px;
    float: left;
    margin-left: 200px;
    margin-top: 150px;
}
.banner .banner-text {
    float: right;
    padding-right: 400px;
    padding-top: 150px;
}
.banner .banner-text h1 {
      font-weight: bold;
      font-size: 60px;
      margin-bottom: 20px;
}
.banner .banner-text p {
      font-size: 20px;
}
.features {
  text-align: center;
  padding: 50px 0 20px;
}
.features img {
    width: 1000px;
}



